
import {Component, OnInit, ViewChild} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import { ChargerTypeService } from 'app/shared/services/charger-type/charger-type.service';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog,MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import {MessageConstant} from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import {FuseConfirmDialogComponent} from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
import { ClientService } from 'app/shared/services/client/client.service';
import * as XLSX from 'xlsx';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';


export class ClientModule {
  id: string;
  name: string;
  status: string;
  created_date: string;
  createdby: number;
  modify_date: string;
  modifyby: number;
  statusString:string;
  otp_authentication:number;
  payment_allowed:number;
  penalty_allowed: number;
reward_allowed: number;
booking_allowed: number;
invoice_allowed: number;
  otp_authenticationString:string;
  payment_allowedString:string;
  penalty_allowedString:string;
  reward_allowedString:string;
  booking_allowedString:string;
  invoice_allowedString:string;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class ListComponent implements OnInit {
  filterEntity: ClientModule;
  filterType: MatTableFilter;
  displayedColumns: string[] = [ 'name','otp_authentication','payment_allowed','penalty_allowed','reward_allowed','booking_allowed',
          'invoice_allowed', 'status','action'];
  dataSource: MatTableDataSource<ClientModule>;
  head = [['Clinet Name', 'OTP Authentication','Payment Allowed','Penalty  Allowed','Reward Allowed','Booking Allowed',
        'Invoice Allowed','Status']];

  clientModules : ClientModule[];
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  dialogRef: any;
  nameSearch = false;
  otpSearch = false;
  paymentSearch = false;
  panaltySearch  = false;
  rewardSearch = false;
  bookingSearch = false;
  invoiceSearch = false;
  statusSearch = false;
  exportedData: any[];
  localStorage : any;
  constructor(private clientService : ClientService,public dialog : MatDialog,
    private snackBar : MatSnackBar) {
    // Create 100 users
    // const users = Array.from({length: 100}, (_, k) => createNewUser(k + 1));

    // Assign the data to the data source for the table to render
  }

  ngOnInit() {
    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    this.getChargerTypes();
    this.filterEntity = new ClientModule();
           this.filterType = MatTableFilter.ANYWHERE;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getChargerTypes(){
    this.clientService.getClientModuleMapping().subscribe(
      (resp:any)=>{
        if(resp != null){
          this.clientModules = resp.data;
        
          for(var i=0; i < this.clientModules.length;i++){
            if(this.clientModules[i].status == 'N'){
              this.clientModules[i].statusString = 'Inactive';
            }else{
              this.clientModules[i].statusString = 'Active';
            }

            if(this.clientModules[i].otp_authentication == 0){
              this.clientModules[i].otp_authenticationString = 'No';
            }else{
              this.clientModules[i].otp_authenticationString = 'Yes';
            }

            if(this.clientModules[i].payment_allowed == 0){
              this.clientModules[i].payment_allowedString = 'No';
            }else{
              this.clientModules[i].payment_allowedString = 'Yes';
            }

            if(this.clientModules[i].penalty_allowed == 0){
              this.clientModules[i].penalty_allowedString = 'No';
            }else{
              this.clientModules[i].penalty_allowedString = 'Yes';
            }

            if(this.clientModules[i].reward_allowed == 0){
              this.clientModules[i].reward_allowedString = 'No';
            }else{
              this.clientModules[i].reward_allowedString = 'Yes';
            }

            if(this.clientModules[i].booking_allowed == 0){
              this.clientModules[i].booking_allowedString = 'No';
            }else{
              this.clientModules[i].booking_allowedString = 'Yes';
            }

            if(this.clientModules[i].invoice_allowed == 0){
              this.clientModules[i].invoice_allowedString = 'No';
            }else{
              this.clientModules[i].invoice_allowedString = 'Yes';
            }
          }
          
          this.dataSource = new MatTableDataSource(this.clientModules);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        }else{
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss',{duration : 3000 })
          return;
        }
        
      },(err)=>{
      }
    )
  }

  delete(id){
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
  });

  //this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

  this.confirmDialogRef.afterClosed().subscribe(result => {
      if ( result )
      {
        this.clientService.delete(id).subscribe(
          (data:any)=>{
            this.getChargerTypes();
            this.snackBar.open(MessageConstant.deleteMessage,'Dismiss',{duration:2000});
          },(err)=>{
        } 
        )
      }
      this.confirmDialogRef = null;
  });
    
  }


  openDialog(action,data): void {
    
    let params = {
      action : action,
      data : data
    }

    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog' ,
      autoFocus: false,
      disableClose: true 
    });

    dialogRef.afterClosed().subscribe(result => {
      
      // this.moderate(result);
      this.getChargerTypes();
    });
  }
  viewDialog(action,data) : void{
    let params = {
      action : action,
      data : data
    }

    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true    
    });

    dialogRef.afterClosed().subscribe(result => {
      
      // this.moderate(result);
      this.getChargerTypes();
    });
  }
  changeVisibility(visibleOption,ev){
    // visibleOption = !visibleOption;
    //nameSearch = false;
      //descSearch = false;
      ev.stopPropagation();
    if(visibleOption == 'nameSearch'){
      this.nameSearch = !this.nameSearch;
      this.filterEntity.name = "";
    }
    
    if(visibleOption == 'statusSearch'){
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString= "";
    }
    
    if(visibleOption == 'otpSearch'){
      this.otpSearch = !this.otpSearch;
      this.filterEntity.otp_authenticationString= "";
    }

    if(visibleOption == 'paymentSearch'){
      this.paymentSearch = !this.paymentSearch;
      this.filterEntity.payment_allowedString= "";
    }


    if(visibleOption == 'panaltySearch'){
      this.panaltySearch = !this.panaltySearch;
      this.filterEntity.penalty_allowedString= "";
    }

    if(visibleOption == 'rewardSearch'){
      this.rewardSearch = !this.rewardSearch;
      this.filterEntity.reward_allowedString= "";
    }

    if(visibleOption == 'bookingSearch'){
      this.bookingSearch = !this.bookingSearch;
      this.filterEntity.booking_allowedString= "";
    }
    if(visibleOption == 'invoiceSearch'){
      this.invoiceSearch = !this.invoiceSearch;
      this.filterEntity.invoice_allowedString= "";
    }
   }
   tableExport()          
   {
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
       var clientModule = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = {'Clinet Name':clientModule.name,'OTP Authentication': clientModule.otp_authenticationString ,
      'Payment Allowed': clientModule.payment_allowedString ,
      'Penalty  Allowed': clientModule.penalty_allowedString ,
      'Reward Allowed': clientModule.reward_allowedString ,
      'Booking Allowed': clientModule.booking_allowedString ,
      'Invoice Allowed': clientModule.invoice_allowedString ,
      'Status':clientModule.statusString};
      this.exportedData.push(data);
    }
     const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
       const workBook: XLSX.WorkBook = XLSX.utils.book_new();
      
       XLSX.utils.book_append_sheet(workBook, workSheet, 'SheetName');
       XLSX.writeFile(workBook, 'Client Module Mapping.xlsx');
     
   }

   pdfExport(){
    var doc = new jsPDF({ // set needed dimensions for any elemen
      
    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var clientModule = this.dataSource.filteredData[i];
     //this.exportedData.push({name: `name${i}`, description: `description${i}`,
     //statusString: `statusString${i}`});
     var data =[clientModule.name, clientModule.otp_authenticationString, clientModule.payment_allowedString,
      clientModule.penalty_allowedString, clientModule.reward_allowedString, clientModule.booking_allowedString,
      clientModule.invoice_allowedString, clientModule.statusString] ;
     
     this.exportedData.push(data);
   }
   var img = new Image();
   var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
   imgRight.src =srcSparkEV;
   var createdDate = new Date().toLocaleString(('en-GB'));
   img.src = src;
   doc.setFontSize(11);
   doc.setTextColor(100);
   doc.setFontSize(18);
   
    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla',fillColor:[11, 148, 68]},
      styles: {
        cellPadding: 1,
        fontSize: 8,
        
      },
     
      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage:data=>{
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);
        
        doc.text('Client Module Mapping Report' ,doc.internal.pageSize.getWidth() / 2, 11, { align: 'center'});
        
        doc.setTextColor(211,211,211);
        doc.setFontSize(60);
        doc.setGState( doc.GState({opacity: 0.6}));
        doc.text('Confidential/Not for Public use',25,285,null,60, );
        doc.setGState( doc.GState({opacity: 1}));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:'+ createdDate ,15, 18);
        
        doc.text('Generated By:'+ this.localStorage.f_Name ,195, 18,{ align: 'right'});
        doc = this.addWaterMark(doc);
      }
    })
  
    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')
    
    // below line for Download PDF document  
    doc.save('Client Module Mapping report.pdf');
   
  }
  addWaterMark(doc){
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
        doc.setFontSize(10);
     //Print Page 1 of 4 for example
     
     doc.text('Page ' +  String(i) + ' of ' + String(newpage),210-20,297-10,null,null,"right")
    return doc;
  
  }


}

