import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { Observable } from 'rxjs';
import { ClientService } from 'app/shared/services/client/client.service';
import { map, startWith } from 'rxjs/operators';

@Component({
  selector: 'app-view-dialog',
  templateUrl: './view-dialog.component.html',   
  styleUrls: ['./view-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class ViewDialogComponent implements OnInit {

  params: any;
  isViewMode = false;
  form: FormGroup;
  localStorage : any;
  isEditMode = false;
  clients: any;
  clientID: any;
  clientServiceMapping: any;
  filteredOptionsClient: Observable<any[]>;
  messageConstant: MessageConstant;

  status = false;
  booking = false;
  payment = false;
  reward = false;
  penalty = false;
  otpAuth = false;
  invoice = false;

  @ViewChild("toggleElement") ref: any;
  @ViewChild("toggleBooking") bookingref: any;
  @ViewChild("togglePayment") paymentref: any;
  @ViewChild("toggleReward") rewardref: any;
  @ViewChild("togglePenalty") penaltyref: any;
  @ViewChild("toggleOtpAuth") otpauthref: any;
  @ViewChild("toggleInvoice") invoiceref: any;

  constructor(private fb: FormBuilder, private snackBar: MatSnackBar, private clientService: ClientService,
    public dialogRef: MatDialogRef<ViewDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: MtxDialogData) { }

  ngOnInit() {
    this.initialize();
  }

  initialize(){

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));

    this.params = this.data;
    
    this.isViewMode = this.params.action == 'CREATE' ? false : true;

    this.form = this.fb.group({
      client: ['', Validators.required],
      booking: this.booking == true ? 1 : 0,
      payment: this.payment == true ? 1 : 0,
      reward: this.reward == true ? 1 : 0,
      penalty: this.penalty == true ? 1 : 0,
      otpAuth: this.otpAuth == true ? 1 : 0,
      invoice: this.invoice == true ? 1 : 0,
      status: this.status == true ? 'Y' : 'N'
    });

    if (this.isViewMode){
      this.assignValues(this.params);
    }
    this.getClients();
    
  }

  close() {
    this.dialogRef.close();
  }

  closeActionPanel(){
    this.dialogRef.close();
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }

  assignValues(params) {
    this.form.get('client').setValue(params.data.client_id);
    this.booking = params.data.booking_allowed == 1 ? true : false;
    this.payment = params.data.payment_allowed == 1 ? true : false;
    this.invoice = params.data.invoice_allowed == 1 ? true : false;
    this.reward = params.data.reward_allowed == 1 ? true : false;
    this.penalty = params.data.penalty_allowed == 1 ? true : false;
    this.otpAuth = params.data.otp_authentication == 1 ? true : false;
    this.status = params.data.status == 'Y' ? true : false;
    }

  getClients() {
    this.clientService.getActiveClientsCW(this.localStorage.userId).subscribe(
      (resp: any) => {
        this.clients = resp;
        if (this.isViewMode) {
          const toSelect = this.clients.find(c => c.id == this.params.data.client_id);
          this.form.get('client').setValue(toSelect);
          this.filteredOptionsClient = this.form.controls.client.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterClientModels(name) : this.clients)
            );
        }

      }, (err) => {

      }
    )
  }

  onClientChange(client) {
    this.clientService.getClientModuleMappingByClientId(client.id).subscribe(
      (resp: any) => {
        if (resp.data.length > 0) {
          this.otpAuth = resp.data[0].otp_authentication == '1' ? true : false;
          this.booking = resp.data[0].booking_allowed == '1' ? true : false;
          this.payment = resp.data[0].payment_allowed == '1' ? true : false;
          this.reward = resp.data[0].reward_allowed == '1' ? true : false;
          this.penalty = resp.data[0].penalty_allowed == '1' ? true : false;
          this.invoice = resp.data[0].invoice_allowed == '1' ? true : false;
        } else {
          this.otpAuth = false;
          this.booking = false;
          this.payment = false;
          this.reward = false;
          this.penalty = false;
          this.invoice = false;
        }
      }, (err) => {

      }
    )

  }

  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }
  updateBooking(event) {
    this.booking = event.checked;
    this.bookingref.checked = this.booking;
  }
  updatePayment(event) {
    this.payment = event.checked;
    this.paymentref.checked = this.payment;
  }
  updateReward(event) {
    this.reward = event.checked;
    this.rewardref.checked = this.reward;
  }
  updatePenalty(event) {
    this.penalty = event.checked;
    this.penaltyref.checked = this.penalty;
  }
  updateOtpAuth(event) {
    this.otpAuth = event.checked;
    this.otpauthref.checked = this.otpAuth;
  }
  updateInvoice(event) {
    this.invoice = event.checked;
    this.invoiceref.checked = this.invoice;
  }

  displayClient(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterClientModels(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.clients.filter(option => option.name.toLowerCase().includes(filterValue));
  }
}





