import { ListComponent } from './list/list.component';
import { ActionDialogComponent } from './action-dialog/action-dialog.component';
import { ViewDialogComponent } from './view-dialog/view-dialog.component';
import { ClientModuleMappingCrudComponent } from './client-moduleMapping-crud/client-moduleMapping-crud.component';
import { ClientModuleMappingRoutingModule } from './client-moduleMapping-routing.module';
import { FuseSharedModule  } from '@fuse/shared.module';
const COMPONENTS =[ClientModuleMappingCrudComponent, CreateComponent, ListComponent];
