import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TABS_DEMO_ROUTES } from './client-moduleMapping-crud/routes';
import { ClientModuleMappingCrudComponent } from './client-moduleMapping-crud/client-moduleMapping-crud.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : ClientModuleMappingCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ClientModuleMappingRoutingModule{}